import React, { Component } from 'react';
import { Button, StyleSheet, View,Text } from 'react-native';

export default class ButtonBasics extends Component {
 
  constructor(props) {
    super(props);
    this.state = {
      dobasszama: 3,
      oldalszam:6,
      szint:1,
      ero:1
    };
  }

 
  _onPressButton=()=> {
    //alert('You tapped the button!')
    var veletlen=Math.floor(Math.random() * this.state.oldalszam) + 1;
    this.setState(
      { dobasszama:veletlen }
    )
  }
  plusz=()=> {
    //alert('You tapped the button!')
    if (this.state.oldalszam<20){
    this.setState(
      { oldalszam:this.state.oldalszam+1 }
    )}
  }
  minusz=()=> {
    //alert('You tapped the button!')
    if (this.state.oldalszam>1){
      this.setState(
        { oldalszam:this.state.oldalszam-1 }
      )}
  }
  pluszszint=()=> {
    //alert('You tapped the button!')
    if (this.state.szint<20){
    this.setState(
      { szint:this.state.szint+1 }
    )}
  }
  minuszszint=()=> {
    //alert('You tapped the button!')
    if (this.state.szint>1){
      this.setState(
        { szint:this.state.szint-1 }
      )}
  }
  pluszero=()=> {
    //alert('You tapped the button!')
    if (this.state.ero<20){
    this.setState(
      { ero:this.state.ero+1 }
    )}
  }
  minuszero=()=> {
    //alert('You tapped the button!')
    if (this.state.ero>1){
      this.setState(
        { ero:this.state.ero-1 }
      )}
  }
  render() {
    return (
      <View style={styles.container}>

<View style={{flex: 1, flexDirection: 'row'}}>
        <View style={styles.felso} >
        <Text style={{padding: 10, fontSize: 10,textAlign:'center',backgroundColor:'darkred',color:'white'}}>
          Szintek száma:
        </Text>
        <View style={styles.felsogomb}>
          <Button
            onPress={this.pluszszint}
            title="+"
          />
        </View>
        <Text style={{padding: 10, fontSize: 10,textAlign:'center',backgroundColor:'blue',marginLeft:15,marginRight:15}}>
          {this.state.szint}
        </Text>
        <View style={styles.felsogomb}>
          <Button
            onPress={this.minuszszint}
            title="-"
          />
        </View>

        </View>
        <View style={styles.felso} >
        <Text style={{padding: 10, fontSize: 10,textAlign:'center',backgroundColor:'darkred',color:'white'}}>
          Erő száma:
        </Text>
        <View style={styles.felsogomb}>
          <Button
            onPress={this.pluszero}
            title="+"
          />
        </View>
        <Text style={{padding: 10, fontSize: 10,textAlign:'center',backgroundColor:'blue',marginLeft:15,marginRight:15}}>
          {this.state.ero}
        </Text>
        <View style={styles.felsogomb}>
          <Button
            onPress={this.minuszero}
            title="-"
          />
        </View>
        </View>
       
      </View>

<View style={{borderBottomWidth:1,borderColor:'black'}} />

      <Text style={{padding: 10, fontSize: 20,textAlign:'center'}}>
          Kocka oldalszáma:
        </Text>
        <View style={styles.kisgomb}>
          <Button
            onPress={this.plusz}
            title="+"
          />
        </View>
        <Text style={{padding: 10, fontSize: 20,textAlign:'center',backgroundColor:'blue',marginLeft:70,marginRight:70}}>
          {this.state.oldalszam}
        </Text>
        <View style={styles.kisgomb}>
          <Button
            onPress={this.minusz}
            title="-"
          />
        </View>
        <View style={styles.kekgomb}>
          <Button
            onPress={this._onPressButton}
            title="Kockadobás"
          />
        </View>
        <Text style={{padding: 10, fontSize: 20,textAlign:'center'}}>
          Kockadobás eredménye:
        </Text>
        <Text style={{padding: 10, fontSize: 30,textAlign:'center',fontWeight:'bold'}}>
          {this.state.dobasszama}
        </Text>
        
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
   flex: 1,
   justifyContent: 'center',
  },
  kekgomb: {
    margin: 20
  },
  kisgomb: {
    margin: 20,
    marginLeft:100,
    marginRight:100
  },
  felsogomb: {
    margin: 20,
    marginLeft:15,
    marginRight:15
  },
  alternativeLayoutButtonContainer: {
    margin: 20,
    flexDirection: 'row',
    justifyContent: 'space-between'
  },
  felso:{
    flex:1,
    margin:5,
    padding:10,
    backgroundColor:'#a9a9a9'
  }

});